<template>
  <div class="achievement-spec-block">
    <li
      class="achievement-list__item"
      v-for="(participant, key) in reports"
      :key="key"
    >
      <div class="achievement-list__item--read">
        <div class="achievement-list__item--tools">
          <span class="count">{{ key + 1 }}</span>
          <a-tooltip placement="top">
            <template slot="title">
              <span>Удалить участника</span>
            </template>
            <v-icon
              @click="deleteReport(key)"
              class="remove"
              name="times"
            ></v-icon>
          </a-tooltip>
        </div>
        <div class="achievement-list__item--read--item item-name">
          <span>{{ participant.name }}</span>
        </div>
        <div
          class="achievement-list__item--read--item item-participation"
        >
          <span>{{ participant.rating }}</span>
        </div>
      </div>
    </li>
    <li class="achievement-list__item">
      <div class="achievement-list__item--info">
        <div
          class="achievement-list__item--info--block achievement-form-multiple"
        >
          <x-form
            class="achievement-spec-block--form"
            v-model="reportForm"
            @submit="addReport"
            name="reportForm"
          >
          
            <div class="achievement-spec-block--form-item">
          <span class="count">{{ reports.length + 1 }}</span>
              <x-input
                name="name"
                type="text"
                validation="isFullName"
                label="Название предмета"
                class="item-name"
                error-behavior="submit"
                :validation-rules="{
                  isFullName: ({ value }) => {
                    return value.trim().split(' ').length === 3
                  },
                }"
                :validation-messages="{
                  isFullName: 'Введите ФИО',
                }"
              ></x-input>
              <x-input
                name="rating"
                type="select"
                validation="required"
                :options="rating"
                label="Оценка"
                class="item-participation"
                error-behavior="submit"
              >
              </x-input>
            </div>
            <a-button
              type="info"
              block
              html-type="submit"
              class="document-container-button"
            >
              <v-icon name="plus"></v-icon>
              <span>Добавить</span>
            </a-button>
          </x-form>
        </div>
      </div>
    </li>
  </div>
</template>

<script>
export default {
  props: {
    reports: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      reportForm: {},
      rating: [2,3,4,5]
    }
  },
  methods: {
    addReport() {
      this.$emit('addReport', {...this.reportForm})
      setTimeout(() => {
        this.reportForm = {}
      }, 0)
    },
    deleteReport(key) {
      this.$emit('removeReport', key)
    },
  },
}
</script>

